:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: .25s;
}

a {
  color: #000;
  text-decoration: none;
}

.gallery {
  max-width: 1800px;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-auto-rows: 320px;
  justify-content: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.gallery__container {
  position: relative;
}

.gallery__item {
  height: 250px;
  display: inline-block;
  position: relative;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  height: 70px;
  border: 1px solid #f5f5f5;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 5px;
  display: flex;
  box-shadow: 0 2px 3px #9e9e9e;
}

.info-item {
  text-align: center;
}

.info-item__header {
  font-weight: 700;
  text-decoration: none;
}

